import React, { useEffect, useState } from 'react'
import "./UserList.css"
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import EditIcon from '@mui/icons-material/Edit';
import { useSnackbar } from 'notistack'; 

export default function UserList({CurrentPageUser,userList,setUserList,checkItem,setcheckItem,setShowEditForm}) {
  const { enqueueSnackbar } = useSnackbar();
  
  
  if(CurrentPageUser){
     

    const deleteCurrentItem=(tarId)=>{
      let data=userList;
      let list=data.filter((e)=>{
        return e.id!=tarId.target.id ;
      })
      setUserList(list);
      enqueueSnackbar("Deleted Sucessfully",{variant:"success"})
    }
    const AddChecklist=(checkbox)=>{
      let data=checkItem;
       
      if(!checkbox.target.checked){
        let list=data.filter((e)=>{
          return e!=checkbox.target.value;
        })
        setcheckItem(list);
      } else{
        setcheckItem([...checkItem,checkbox.target.value]);
      }
    }
    const CheckAll=(e)=>{
       if (e.target.checked==true){
        let data=[];
        CurrentPageUser.forEach(element => {
          data.push(element.id)
        }); 
        setcheckItem(data);
       }else if(e.target.checked==false){
        let data=[];
        setcheckItem(data);
       } 
    }
      
    
    
    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };
    return (
    <div className='UserListPage'>
        <table className='Table'>
            <thead className='TableHeader'>
             <tr className='TableRow'>
             <th className="tableBottom"> 
             <label className="container">
               <input type="checkbox" checked={checkItem.length<10?false:true} onClick={(e)=>{CheckAll(e)}} />
                    <span className="checkmark"></span>
             </label> </th>
             <th>Name</th>   
             <th>Email</th>
             <th>Role</th>
             <th>Actions</th>
             </tr>
            </thead>
            <tbody>
            {CurrentPageUser.map((e)=>(
             <tr key={e.id} className='TableRow'> 
              <th><label className="container">
               <input type="checkbox" value={e.id} checked={checkItem.includes(e.id)}  onChange={(e)=>{AddChecklist(e)}}  />
                    <span className="checkmark"></span>
             </label></th>
              <td>{e.name}</td>
              <td>{e.email}</td>
              <td>{e.role}</td>
              <td><button className='edit' onClick={()=>setShowEditForm({show:true,id:e.id})}><EditIcon fontSize='small'/></button><button className='delete_icon'   onClick={(e)=>{deleteCurrentItem(e)}} ><DeleteOutlineIcon fontSize='small' id={e.id}/></button></td>
             </tr>
             
        ))}
        </tbody>
        </table>
        
    </div>
  )
        }
}
