import React, { useState } from 'react'
import { Dialog, DialogActions, DialogContent, DialogTitle } from '@mui/material'
import "./EditForm.css"
import { useSnackbar } from 'notistack'; 
function EditForm({ShowEditForm,setShowEditForm,userList,setUserList}) {
    const { enqueueSnackbar } = useSnackbar();
    const [tableData,setTableData]=useState(
        {name:"",email:"",role:"" }
    )
    const EditChangesOnList=(tableData)=>{
        setShowEditForm({show:false,id:""});
        enqueueSnackbar("Changes Saved Sucessfully",{variant:"success"})
    } 
  return (
    <div>
      <Dialog open={ShowEditForm.show}  className="UploadContainer" >
      <DialogTitle>
        Edit Employee Details of employee ID {ShowEditForm.id}: 
      </DialogTitle>

      <DialogContent>
      <table className='Table'>
        <thead className='TableHeader'>
          <tr className='TableRow'>
             <th>Name</th>   
             <th>Email</th>
             <th>Role</th>
          </tr>
        </thead>
        <tbody>
          <tr> 
            <td><input type="text" value={tableData.name} onChange={(e)=>setTableData({...tableData,name:e.target.value})}/> </td>
            <td><input type="text" value={tableData.email} onChange={(e)=>setTableData({...tableData,email:e.target.value})} /> </td>
            <td><input type="text" value={tableData.role} onChange={(e)=>setTableData({...tableData,role:e.target.value})}/> </td>
          </tr>     
        </tbody>
        </table>
      </DialogContent> 

      <DialogActions className='Btn_sec'>
        <button className='submit_btn' onClick={()=>EditChangesOnList(tableData)} >Submit</button>
        <button className='cancel_btn' onClick={()=>setShowEditForm({show:false,id:""})}>Cancel</button>
      </DialogActions>
      </Dialog>
    </div>
  )
}

export default EditForm
