
import SearchBar from './SearchBar.js';
import UserList from './UserList.js';
import { useEffect, useState } from 'react';
import axios from "axios"
import { useSnackbar } from 'notistack'; 
import React from 'react'
import Pagination from './Pagination.js';
import EditForm from './EditForm.js';

export default function Dashboard() {
    const Config={"endpoint":"https://geektrust.s3-ap-southeast-1.amazonaws.com/adminui-problem/members.json"}; 
  const [userList,setUserList]=useState([]);
  const [totalNoPages,setTotalNoPages]=useState(); 
  const [currentPage,setCurrentPage]=useState(1);
  const [userPerPage,setUserPerPage]=useState(10);
  const [SearchResult,setSearchResult]=useState("");
  const [CurrentUserList,setCurrentUserList]=useState([]);
  const [checkItem,setcheckItem]=useState([]);
  const [ShowEditForm,setShowEditForm]=useState({
    show:false,id:""
  });
  const { enqueueSnackbar } = useSnackbar();
  
  useEffect(()=>{
    FetchUserList();
  },[])
  
  useEffect(()=>{
    if(!SearchResult){
      let data=userList;
      
     setCurrentUserList(data);
    //  console.log(CurrentUserList);
     setTotalNoPages(Math.ceil((data.length-1)/10));
     setCurrentPage(1)
    }else if(SearchResult){
     let SearchArr=userList;
     let data= SearchArr.filter((e)=>{
        return e.name.toLowerCase().includes(SearchResult)||e.email.toLowerCase().includes(SearchResult)||e.role.toLowerCase().includes(SearchResult)
      })
      setCurrentUserList(data);
      setTotalNoPages(Math.ceil(data.length/10));
      if(currentPage>totalNoPages){
        setCurrentPage(totalNoPages)
       }
    }
  },[SearchResult,userList])
  
   
    const FetchUserList=async()=>{
    let data= await axios.get(Config.endpoint) ;
     setUserList(data.data);
     setCurrentUserList(data.data);
     setTotalNoPages(Math.ceil(data.data.length/10));
    }
     
    const DeleteSelectedUsers=(checkItem,setcheckItem,userList,setUserList)=>{
       let data=userList;
       let check=checkItem;
       let list=data.filter((e)=>{
        return !check.includes(e.id);
       })
        setUserList(list);
        let clearcheckItem=[];
        setcheckItem(clearcheckItem);
        enqueueSnackbar("Deleted Sucessfully",{variant:"success"})
    }
     
   
      let IndexOfLastUser= currentPage*userPerPage;
      let IndexOfFirstUser= IndexOfLastUser-userPerPage;
      let CurrentPageUser=CurrentUserList.slice(IndexOfFirstUser,IndexOfLastUser);
    
    
    
  return (
    <div>
      <SearchBar SearchResult={SearchResult} setSearchResult={setSearchResult}/>
      <UserList  CurrentPageUser={CurrentPageUser} userList={userList} setUserList={setUserList} checkItem={checkItem}  setcheckItem={setcheckItem} setShowEditForm={setShowEditForm}/>
      <Pagination currentPage={currentPage} setCurrentPage={setCurrentPage} totalNoPages={totalNoPages} userPerPage={userPerPage} DeleteSelectedUsers={DeleteSelectedUsers} checkItem={checkItem} setcheckItem={setcheckItem} userList={userList} setUserList={setUserList}/>
      <EditForm ShowEditForm={ShowEditForm} setShowEditForm={setShowEditForm} userList={userList} setUserList={setUserList} />
    </div>
  )
}
